.tech-block {
  background-color: white;
  width: 30%;
  min-width: 90px;
  margin: 1%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.invert {
  background-color: black;
  border: 1px solid white;
}

.tech-link {
  text-decoration: none;
  font-size: 48px;
  color: black;
}

.invert .tech-link {
  text-decoration: none;
  color: white;
}

.tech-block:hover {
  -webkit-filter: invert();
          filter: invert();
  border: 1px solid black;
}

.tech-block:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

@media (max-width: 1000px) {
  .tech-block {
    width: 45%;
  }

  .tech-link {
    text-decoration: none;
    font-size: 40px;
  }
}

@media (max-width: 560px) {
  .tech-link {
    text-decoration: none;
    font-size: 24px;
  }
}


