.desktop-only {
  visibility: visible;
}

.no-desktop {
  visibility: hidden;
}

@media (max-width: 1040px) {
  .no-desktop {
    visibility: visible;
  }

  .desktop-only {
    visibility: hidden;
    position: absolute;
  }

  .mdc-tab-bar__indicator {
    visibility: hidden !important;
  }
}


