.wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  margin-bottom: 64px;
}
.container {
  max-width: 90%;
  margin: 0 auto;
}
.one {
  grid-column-start: 1;
  grid-column-end: span 8;
  grid-row-start: 1;
  grid-row-end: span 2;
  background-image: url("/hacksaw.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.two {
  grid-column-start: 1;
  grid-column-end: span 2;
  grid-row-start: 3;
  grid-row-end: span 3;

  background-image: url("/para.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.three { 
  grid-column-start: 3;
  grid-column-end: span 2;
  grid-row-start: 3;
  grid-row-end: span 3;
  background-image: url("/wanted.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.four {
  grid-column-start: 5;
  grid-column-end: span 4;
  grid-row-start: 3;
  grid-row-end: span 5;

  background-image: url("/mach.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.five {
  grid-column-start: 1;
  grid-column-end: span 2;
  grid-row-start: 6;
  grid-row-end: span 7;
  background-image: url("/tusk.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.six {
  grid-column-start: 3;
  grid-column-end: span 2;
  grid-row-start: 6;
  grid-row-end: span 5;
  background-image: url("/byz.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.seven {
  grid-column-start: 3;
  grid-column-end: span 2;
  grid-row-start: 11;
  grid-row-end: span 2;
  background-image: url("/2stand.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
} 

.eight {
  grid-column-start: 7;
  grid-column-end: span 2;
  grid-row-start: 8;
  grid-row-end: span 5;
  background-image: url("/mang.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.nine {
  grid-column-start: 5;
  grid-column-end: span 2;
  grid-row-start: 8;
  grid-row-end: span 2;
  background-image: url("/st12.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.ten {
  grid-column-start: 5;
  grid-column-end: span 2;
  grid-row-start: 10;
  grid-row-end: span 3;
  background-image: url("/sin-city.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.mdc-fab {
  margin: 8px 0 0 8px;
  background-color: black;
  position: absolute;
}

@media (max-width: 1040px) {
  .wrapper {
    grid-template-columns: repeat(4, 1fr);
  }

  .one {
    grid-column-start: 1;
    grid-column-end: span 4;
    grid-row-start: 1;
    grid-row-end: span 2;
  }

  .four {
    grid-column-start: 1;
    grid-column-end: span 4;
    grid-row-start: 6;
    grid-row-end: span 5;
  }

  .five {
    grid-column-start: 1;
    grid-column-end: span 2;
    grid-row-start: 11;
    grid-row-end: span 7;
  }

  .six {
    grid-column-start: 3;
    grid-column-end: span 2;
    grid-row-start: 11;
    grid-row-end: span 5;
  }

  .seven {
    grid-column-start: 3;
    grid-column-end: span 2;
    grid-row-start: 16;
    grid-row-end: span 2;
  }

  .eight {
    grid-column-start: 3;
    grid-column-end: span 2;
    grid-row-start: 18;
    grid-row-end: span 5;
  }

  .nine {
    grid-column-start: 1;
    grid-column-end: span 2;
    grid-row-start: 18;
    grid-row-end: span 2;
  }

  .ten {
    grid-column-start: 1;
    grid-column-end: span 2;
    grid-row-start: 20;
    grid-row-end: span 3;
  }
}



