.video-wrapper {
  background-color: black;
}

.screen {
  position:relative;
  padding: 56.25% 0 0;
  background-color: black;
  margin: 0 auto;
}

.tv {
  width:100%;
  height:100%;
  position:absolute;
  left:0;
  top:0;
  right:0;
  bottom:0;
}

@media (min-width: 1920px) {
  .screen {
    position:relative;
    padding: 0;
    width: 1920px;
    height: 1080px;
    background-color: black;
    margin: 0 auto;
  }
}


